.content__detail {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 2%;
  margin-bottom: 5%;
  .info__detail {
    width: 40%;

    .list-group-item {
      border: none;
    }
    .dash {
      width: 95%;
      border-bottom: dashed;
      opacity: 0.7;
      margin: 0 3.2%;
      padding: 0;
    }
    .child__detail {
      margin: 1% 0;
    }
    .id__status {
      display: flex;
      justify-content: space-between;
    }

    .price {
      color: #f15e2c;
      font-size: 30px;
      position: relative;
    }

    .price::after {
      position: absolute;
      content: "";
      width: 100%;
      bottom: -25%;
      left: 0%;
    }

    .color__product {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }

    .color__product::after {
      position: absolute;
      content: "";
      width: 100%;
      bottom: -50%;
      left: 0%;
    }

    .color__product::before {
      position: absolute;
      content: "";
      width: 100%;
      top: -50%;
      left: 0%;
    }

    .child__detail {
      // margin: 30px;
      text-align: left;
    }
    .size__amount {
      display: flex;
      font-weight: bold;
      div {
        font-size: 1rem;
        margin-right: 20%;

        // width: 40%;
        // .size__option {
        //   // width: 50%;
        //   :last-child {
        //     width: 100%;
        //     margin: 0;

        //   }
        //   // font-weight: bold;
        //
        // }
      }
    }
    .btn__addCart__like {
      width: 100%;
      display: flex;
      justify-content: space-between;
      padding-bottom: 0;
      .btn__addCart {
        width: 70%;
        padding: 1% 0;
        .btn__add {
          padding: 6% 10%;
          width: 100%;
          height: auto;
          font-size: 1.7ch;
          background-color: #111;
          color: #fff;
        }
      }

      .btn__like {
        width: 25%;
        padding: 1% 0;
        .btn__heart {
          padding: 4%;
          width: 100%;
          height: 100%;
          font-size: 1.2em;
          background-color: #111;
          color: #fff;
        }
      }
    }

    .btn__payment {
      padding-top: 0;
      .btn__pay {
        padding: 4%;
        width: 100%;
        height: auto;
        font-size: 2.7ch;
        background-color: #f15e2c;
        color: #fff;
        border-radius: 3px;
      }
    }
    .warning {
      color: #f15e2c;
      font-size: 22px;
      height: 80%;
    }

    .panel {
      padding: 0;
      border: none;
      margin: 0 4%;
    }
  }
}

